
import { defineComponent, ref, watch, computed, nextTick } from 'vue'
import { getShareUrl, getPublishInfo, publishApp } from '@/api/project'
import { MessageUtil } from '@/utils/message-util'
import { copyText } from '@/utils/util'
import { UPDATE_MODEL_EVENT } from '@/utils/constants'

export default defineComponent({
  name: 'PublishScreen',
  props: {
    projectId: {
      type: Number,
      required: true,
    },
    modelValue: {
      type: Boolean,
      required: true,
    },
  },
  emits: [UPDATE_MODEL_EVENT],
  setup(props, ctx) {
    const visible = ref(false)
    const isPublish = ref(false)
    const showTooltip = ref(false)
    const password = ref('')
    const passwordType = ref('password')
    const loading = ref(true)

    const shareUrl = computed(() => {
      return isPublish.value ? getShareUrl(props.projectId) : ''
    })

    const copyUrl = () => {
      if (isPublish.value) {
        nextTick(() => {
          if (copyText(shareUrl.value)) {
            MessageUtil.success('复制成功')
          }
        })
      }
    }

    const closeDialog = () => {
      visible.value = false
      ctx.emit(UPDATE_MODEL_EVENT, false)
    }

    watch(() => props.modelValue, val => {
      if (val) {
        visible.value = true
      } else {
        visible.value = false
      }
    })

    watch(() => props.projectId, async () => {
      try {
        const res = await getPublishInfo(props.projectId)
        if (res.data.code === 0) {
          const { data } = res.data
          loading.value = false
          isPublish.value = !!data.share
          password.value = data.password
        }
      } catch (error) {
        console.log(error)
      }
    })

    const publish = async () => {
      try {
        loading.value = true
        const data = {
          share: shareUrl.value,
          password: password.value,
        }
        const res = await publishApp(props.projectId, data)
        if (res.data.code === 0) {
          loading.value = false
          closeDialog()
          MessageUtil.success(isPublish.value ? '发布成功' : '保存成功')
        }
      } catch (error) {
        MessageUtil.error('发布失败')
      } finally {
        loading.value = false
      }
    }

    return {
      visible,
      isPublish,
      shareUrl,
      showTooltip,
      password,
      copyUrl,
      closeDialog,
      loading,
      passwordType,
      publish,
    }
  },
})
